var estilos = {
  normal: 'normal',
  monetario: 'monetario',
  porcentagem: 'porcentagem'
};
module.exports = {
  porExtenso: porExtenso,
  estilo: estilos
};

function porExtenso(numero) {
  var estilo = arguments.length > 1 && arguments[1] !== undefined ? arguments[1] : estilos.normal;
  if (!estilos[estilo]) throw new Error("Estilo \"".concat(estilo, "\" inv\xE1lido. Poss\xEDveis valores: normal, monetario, porcentagem"));
  return require('./lib/estilos/' + estilo)(numero);
}