var common = require('../common'),
    casasDecimais = require('../nomesExtenso/casasDecimais');

module.exports = porExtenso;

function porExtenso(numero) {
  var numberStr = numero.toString().replace('-', '').split('.'),
      numberBefore = numberStr[0] || 0,
      numberAfter = numberStr[1];
  var numberBeforeExtended = (numero < 0 ? 'menos ' : '') + common.numberInFull(numberBefore),
      numberAfterExtended = '';

  if (+numberAfter) {
    numberAfter = numberAfter.replace(/0+$/g, '');
    numberAfterExtended = " v\xEDrgula ".concat(common.numberInFull(numberAfter));
    var casaDecimal = descobreNomeCasaDecimal(numberAfter);
    if (casaDecimal) numberAfterExtended += " ".concat(casaDecimal);
  }

  return numberBeforeExtended + numberAfterExtended;
}

function descobreNomeCasaDecimal(numberAfter) {
  var casaDecimal = casasDecimais[numberAfter.length - 1] || ''; // plural

  if (casaDecimal && +numberAfter !== 1) {
    casaDecimal = casaDecimal.split(' ');
    casaDecimal[0] = casaDecimal[0] + 's';
    casaDecimal = casaDecimal.join(' ');
  }

  return casaDecimal;
}